const app = getApp();
const common = require('../../../publicjs/common.js');
const utils = require('../../../utils/utils.js');
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniIcons from '@/components/uni-icons/uni-icons.vue';
export default {
	components: {
		uniNavBar,
		uniIcons,
	},
	data() {
		return {
			avatar: '', //头像
			nickname: '', //昵称
			money: 0, //提现金额
			identity: '', //身份证
			placeholderClass: {
				textAlign: 'right'
			},
			viewTop: 0,
			marginTop: 0,
			showRight: false,
			showLeft: false
		};
	},
	onShow() {
		this.marginTop = this.$store.state.SET_CUSTOM_BAR + 25 + 'px';
		this.viewTop = this.$store.state.SET_CUSTOM_BAR;
		this.getuserinfo(); //获取系统信息
	},
	watch: {},
	methods: {
		//获取缓存中的系统信息
		getuserinfo() {
			try {
			    const value = uni.getStorageSync('userinfolist');
			    if (value) {
			        console.log(value);
					this.avatar =  value.portrait;
					this.nickname =  value.nickname;
			    }
			} catch (e) {
			    // error
				console.log("获取失败")
			}
		},
		
		//跳转到个人信息页面 - 未实名
		gouserinfo() {
			let param = {
				pagesurl: '/pages/userCentre/index/index'
			}
			param = JSON.stringify(param)
			uni.navigateTo({
				url: '../baseinfo/baseinfo?item=' + param
			})
		},
		//跳转到我的通知页面
		goinform() {
			let param = {
				pagesurl: '/pages/userCentre/index/index'
			}
			param = JSON.stringify(param)
			uni.navigateTo({
				url: '../inform/inform?item=' + param
			})
		},
		//提现
		depositClick() {
			console.log('点击了提现按钮');
		},
	},
	onNavigationBarButtonTap(e) {
		this.showRight = !this.showRight;
	},
	onBackPress() {
		if (this.showRight || this.showLeft) {
			this.hide();
			return true;
		}
	}
};
